                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="new_importtype" label="" show-overflow-tooltip>
                        </el-table-column>
                      </el-table>
                    </div>
                    <!--  -->
                    <div v-else-if="item.entityName === 'new_languageconfig'">
                      <el-table :height="pagingTableHeight" :data="tableData.ecTo" :key="tableKey + '_envirTo'" border
                        style="width: 100%" v-loading="loading" :default-sort="{
                          prop: 'new_name',
                          order: 'ascending',
                        }">
                        <!--  -->
                        <el-table-column type="index" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="new_name" label="" show-overflow-tooltip width="160" sortable>
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="new_language_id" label="" show-overflow-tooltip width="80">
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="new_content" label="" show-overflow-tooltip width="200">
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="new_note" label="" show-overflow-tooltip>
                        </el-table-column>
                      </el-table>
                      <el-pagination :key="tableKey + '_envirToPaging'" :disabled="loading"
                        @size-change="handleSizeChange2" @current-change="handleCurrentChange2"
                        :current-page="input.pageIndexEnvirTo" :page-sizes="PageSizeList"
                        :page-size="input.pageSizeEnvirTo" layout="total, sizes, prev, pager, next, jumper"
                        :total="tableData.ecToTotalRecord">
                      </el-pagination>
                    </div>
                    <!--  -->
                    <div v-else-if="item.entityName === 'documenttemplate'">
                      <el-table :height="pagingTableHeight" :data="tableData.ecTo" :key="tableKey + '_envirTo'" border
                        style="width: 100%" v-loading="loading" :default-sort="{
                          prop: 'name',
                          order: 'ascending',
                        }">
                        <!--  -->
                        <el-table-column type="index" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="name" label="" show-overflow-tooltip width="160" sortable>
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="documenttype" label="" width="130" show-overflow-tooltip>
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="associatedentityName" label="" show-overflow-tooltip width="120">
                        </el-table-column>
                        <!--  -->
                        <el-table-column prop="languageName" label="" show-overflow-tooltip>
                        </el-table-column>
                        <!--  -->
                        <el-table-column fixed="right" label="" show-overflow-tooltip>
                          <template slot-scope="scope">
                            <el-button @click="
                              downloadTemplate(input.envirTo, scope.row)
                              " type="text" size="small"></el-button>
                          </template>
                        </el-table-column>
                      </el-table>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
            </el-tab-pane>
          </el-tabs>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'ConfigurationSync',
  data() {
    // 
    const createdonDR_rule = (rule, value, callback) => {
      if (
        this.nowTabName === "new_languageconfig" &&
        (this.rtcrm.isNull(value) || value.length !== 2)
      ) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      msg: "hello rektec",
      // 
      tabs: [
        {
          label: "",
          entityName: "new_systemparameter",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_autonumber",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_sumrelationshipdetail",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_ribbon",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_duplicatedetect",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_import",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "CommonDeleteCheck",
          entityName: "commondeletecheck",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "new_languageconfig",
          closable: false,
          isSysConfig: true,
        },
        {
          label: "",
          entityName: "documenttemplate",
          closable: false,
          isSysConfig: true,
        },
      ],
      nowTab: null, //Tab
      nowTabName: "", //TabentityName
      // 
      environments: [{ label: "", key: "undefined" }],
      // 
      input: {
        envirFrom: "dev",
        envirTo: "uat",
        pageIndexEnvirFrom: 1,
        pageSizeEnvirFrom: 20,
        pageIndexEnvirTo: 1,
        pageSizeEnvirTo: 20,
        createdonDR: [],
      },
      // 
      PageSizeList: [20, 50, 100, 200],
      tableData: {
        ecFrom: [],
        ecFromTotalRecord: 0,
        ecTo: [],
        ecToTotalRecord: 0,
      }, //
      tableHeight: "380px", //
      tableKey: 1, //Key
      multipleSelection: [], //
      loading: false, //
      retrieveEntityName: "", //
      requestMessage: "", //
      // 
      rules: {
        // 
        createdonDR: [{ validator: createdonDR_rule, trigger: "change" }],
      },
    };
  },
  created() {
    //
    this.$set(this, "nowTab", this.tabs[0]);
    this.$set(this, "nowTabName", this.nowTab.entityName);

    //#region -
    let nowDT = new Date(this.rtcrm.formatDate(new Date(), "yyyy-MM-dd"));
    if (nowDT.getDate() > 15)
      nowDT.setMonth(nowDT.getMonth(), 1);
    else
      nowDT.setMonth(nowDT.getMonth() - 1, 1);
    this.input.createdonDR.push(nowDT);
    this.input.createdonDR.push(
      new Date(this.rtcrm.formatDate(new Date(), "yyyy-MM-dd"))
    );
    //#endregion

    //
    this.getEnvironments();
  },
  mounted() {
    //
    this.$on('environment-change', this.environmentChange);
  },
  computed: {
    // Guid List
    configIdList() {
      let array = [];
      this.multipleSelection.forEach((item) => {
        if (!this.rtcrm.isNullOrWhiteSpace(item.Id)) array.push(item.Id);
        else if (!this.rtcrm.isNullOrWhiteSpace(item.id)) array.push(item.id);
      });
      return array;
    },
    // Table
    pagingTableHeight() {
      let tableHeight = parseInt(this.tableHeight);
      return tableHeight - 30 + "px";
    },
    // 
    createdonRange() {
      if (
        this.rtcrm.isNull(this.input.createdonDR) ||
        this.input.createdonDR.length !== 2
      )
        return "";
      else {
        return (
          this.rtcrm.formatDate(this.input.createdonDR[0], "yyyy-MM-dd") +
          ";" +
          this.rtcrm.formatDate(this.input.createdonDR[1], "yyyy-MM-dd")
        );
      }
    },
  },
  methods: {
    //
    environmentChange: function (envir) {
      if (this.rtcrm.isNullOrWhiteSpace(envir)) return;

      //
      this.$set(this.tableData, "ecFrom", []);
      this.$set(this.tableData, "ecFromTotalRecord", 0);
      this.$set(this.tableData, "ecTo", []);
      this.$set(this.tableData, "ecToTotalRecord", 0);
      this.$set(this, "multipleSelection", []); //
      this.$set(this, "tableKey", this.tableKey + 1); //Table

      //
      this.getEnvironments();
    },

    //
    getEnvironments: function () {
      let _this = this;
      this.$set(this, "loading", true);

      this.jshelper
        .invokeHiddenApiAsync(
          "new_hbxn_common",
          "SyncConfiguration/GetEnvironments",
          null
        )
        .then((res) => {
          _this.$set(_this, "loading", false);
          if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
            this.jshelper.openAlertDialog(this,
              "", ""
            );
            return;
          }
          if (res.isSuccess) {
            let data = res.data;
            if (!this.rtcrm.isNull(data)) _this.$set(_this, "environments", data);
          } else {
            this.jshelper.openAlertDialog(this, res.message, "");
          }
        })
        .catch((err) => {
          _this.$set(_this, "loading", false);
          _this.jshelper.openAlertDialog(_this, err.message, "");
        });
    },

    //
    getData: function (envir) {
      console.log(this.input);
      let input = {};
      let route = "";
      let _this = this;
      this.$refs["form"].validate(async (valid, error) => {
        if (valid) {
          //#region 
          if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirFrom") {
            this.$set(this.tableData, "ecFrom", []);
            this.$set(this.tableData, "ecFromTotalRecord", 0);
          }
          if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirTo") {
            this.$set(this.tableData, "ecTo", []);
            this.$set(this.tableData, "ecToTotalRecord", 0);
          } //
          this.$set(this, "multipleSelection", []); //
          this.$set(this, "tableKey", this.tableKey + 1); //Table
          //#endregion

          //#region 
          switch (this.nowTab.entityName) {
            case "new_systemparameter":
            case "new_autonumber":
            case "new_duplicatedetect":
            case "new_sumrelationshipdetail":
            case "new_ribbon":
            case "commondeletecheck":
            case "new_import":
            case "documenttemplate":
              input["entityName"] = this.nowTab.entityName;
              input["envirFrom"] = this.input.envirFrom;
              input["envirTo"] = this.input.envirTo;
              if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirFrom")
                this.$set(this.input, "pageIndexEnvirFrom", 1); //
              if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirTo")
                this.$set(this.input, "pageIndexEnvirTo", 1); //
              break;
            case "new_languageconfig":
              input["envirFrom"] = this.input.envirFrom;
              input["envirTo"] = this.input.envirTo;
              input["createdonRange"] = this.createdonRange;
              if (this.rtcrm.isNullOrWhiteSpace(envir)) {
                this.$set(this.input, "pageIndexEnvirFrom", 1); //
                this.$set(this.input, "pageIndexEnvirTo", 1); //
              }
              input["pageIndexEnvirFrom"] = this.input.pageIndexEnvirFrom;
              input["pageSizeEnvirFrom"] = this.input.pageSizeEnvirFrom;
              input["pageIndexEnvirTo"] = this.input.pageIndexEnvirTo;
              input["pageSizeEnvirTo"] = this.input.pageSizeEnvirTo;
              break;
          }
          switch (this.nowTab.entityName) {
            case "new_systemparameter":
            case "new_autonumber":
            case "new_duplicatedetect":
            case "new_sumrelationshipdetail":
            case "new_ribbon":
            case "commondeletecheck":
            case "new_import":
            case "documenttemplate":
              route = "SyncConfiguration/GetSystemConfigs";
              break;
            case "new_languageconfig":
              route = "SyncConfiguration/GetLanguageConfigs";
              if (input.hasOwnProperty("entityName"))
                delete input["entityName"];
              break;
          }
          if (this.rtcrm.isNullOrWhiteSpace(route)) return;
          //#endregion

          //#region 
          this.$set(this, "loading", true);
          console.log(input);
          this.jshelper
            .invokeHiddenApiAsync("new_hbxn_common", route, JSON.stringify(input))
            .then((res) => {
              _this.$set(_this, "loading", false);
              _this.$set(_this, "retrieveEntityName", _this.nowTab.entityName); //
              if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
                this.jshelper.openAlertDialog(this,
                  "", ""
                );
                return;
              }
              if (res.isSuccess) {
                let data = res.data;
                if (Array.isArray(data.ecFrom) && Array.isArray(data.ecTo)) {
                  if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirFrom")
                    _this.$set(_this.tableData, "ecFrom", data.ecFrom);
                  if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirTo")
                    _this.$set(_this.tableData, "ecTo", data.ecTo);
                } else {
                  if (
                    this.rtcrm.isNullOrWhiteSpace(envir) ||
                    envir === "envirFrom"
                  ) {
                    _this.$set(_this.tableData, "ecFrom", data.ecFrom.data);
                    _this.$set(
                      _this.tableData,
                      "ecFromTotalRecord",
                      data.ecFrom.TotalRecordCount
                    );
                  }
                  if (this.rtcrm.isNullOrWhiteSpace(envir) || envir === "envirTo") {
                    _this.$set(_this.tableData, "ecTo", data.ecTo.data);
                    _this.$set(
                      _this.tableData,
                      "ecToTotalRecord",
                      data.ecTo.TotalRecordCount
                    );
                  }
                }
              } else {
                this.jshelper.openAlertDialog(this, res.message, "");
              }
              _this.$set(_this, "tableKey", _this.tableKey + 1); //Table
            })
            .catch((err) => {
              _this.$set(_this, "loading", false);
              _this.jshelper.openAlertDialog(_this, err.message, "");
            });
          //#endregion
        }
      });
    },

    //
    handleSelectionChange(val) {
      this.$set(this, "multipleSelection", val); //
    },

    //
    handlerConfirm: function () {
      //#region 
      if (this.rtcrm.isNull(this.configIdList) || this.configIdList.length === 0) {
        this.showMessage("", "warning");
        return;
      } else if (this.retrieveEntityName !== this.nowTab.entityName) {
        this.showMessage("", "warning");
        return;
      }
      //#endregion

      let _this = this;
      this.$set(this.input, "pageIndexEnvirFrom", 1); //
      this.$set(this.input, "pageIndexEnvirTo", 1); //
      let tips = this.getTipsFromConfig(this.nowTab.entityName); //
      if (!this.rtcrm.isNullOrWhiteSpace(tips)) {
        this.jshelper.openConfirmDialog(tips, {}, (parms) => {
          if (!parms.confirmed) return;

          this.jshelper.openConfirmDialog("", {}, (parms2) => {
            if (!parms2.confirmed) return;

            this.jshelper.showLoading();
            this.$set(_this, "loading", true);
            let input = {
              envirFrom: _this.input.envirFrom,
              envirTo: _this.input.envirTo,
              entityName: _this.nowTab.entityName,
              configList: _this.configIdList,
            };

            //#region 
            this.jshelper
              .invokeHiddenApiAsync(
                "new_hbxn_common",
                "SyncConfiguration/SyncSystemConfigs",
                input
              )
              .then((res) => {
                this.jshelper.closeLoading();
                this.$set(_this, "loading", false);
                if (_this.rtcrm.isNull(res)) {
                  this.rtcrm.openAlertDialog(
                    ""
                  );
                  return;
                }
                if (res.isSuccess) {
                  _this.showMessage("", "success", true);
                  _this.getData(); //
                } else {
                  this.jshelper.openAlertDialog(this, res.message, "");
                }
                if (!this.rtcrm.isNullOrWhiteSpace(res.message))
                  _this.addMessageTab(res.message);

                _this.$set(_this, "multipleSelection", []); //
                _this.$set(_this, "tableKey", _this.tableKey + 1); //Table
              })
              .catch((err) => {
                _this.$set(_this, "loading", false);
                _this.jshelper.closeLoading();
                _this.jshelper.openAlertDialog(_this, err.ExceptionMessage ? err.ExceptionMessage : err.message, "");
                _this.addMessageTab(err.message);
              });
            //#endregion
          });
        });
      }
    },

    //Tab Click
    handleTabClick: function (tab, event) {
      if (this.rtcrm.isNull(tab)) return;
      var entityName = this.tabs[tab.index].entityName;
      this.$set(this, "nowTab", this.tabs[tab.index]);
      this.$set(this, "nowTabName", this.nowTab.entityName);
      this.$set(this, "multipleSelection", []); //
      this.$set(this, "tableKey", this.tableKey + 1); //Table
    },

    //Label
    GetEnvirLabel: function (val) {
      let obj = { label: "", key: "undefined" };
      let _obj = this.environments.find((item) => {
        return item.key === val;
      });
      return !this.rtcrm.isNull(_obj) ? _obj : obj;
    },

    //Label
    GetTabLabel: function (val) {
      let obj = {};
      obj = this.tabs.find((item) => {
        return item.entityName === val;
      });
      return obj;
    },

    //envirFromChange
    envirFromChange: function () {
      this.showMessage("", "warning");
    },

    //envirToChange
    envirToChange: function () {
      this.showMessage("", "warning");
    },

    //
    getTipsFromConfig(entityName) {
      let tips =
        "" + this.GetTabLabel(entityName).label + "\n";
      switch (entityName) {
        case "new_systemparameter":
          tips +=
            "1\n2";
          break;
        case "new_autonumber":
          tips += "1\n";
          break;
        case "new_duplicatedetect":
          tips += "1\n";
          break;
        case "new_sumrelationshipdetail":
          tips += "1\n";
          break;
        case "new_ribbon":
          break;
        case "commondeletecheck":
          tips +=
            "1ConfigSecure Config\n";
          break;
        case "new_languageconfig":
          tips +=
            "150\n";
          break;
      }
      return tips;
    },

    //showMessage
    showMessage(message, type, dangerouslyUseHTMLString = false) {
      this.$message({
        message: message,
        dangerouslyUseHTMLString: dangerouslyUseHTMLString,
        type: type,
      });
    },

    //-
    indexMethodLower: function (index) {
      if (index < 0) index = 1;
      else index = index + 1;
      let s = "";
      while (index > 0) {
        let m = index % 26;
        m = m === 0 ? (m = 26) : m;
        s = String.fromCharCode(96 + parseInt(m)) + s;
        index = (index - m) / 26;
      }
      return s;
    },

    //MessageTab
    addMessageTab: function (message) {
      let messageTab = null;
      this.$set(this, "requestMessage", message);
      this.tabs.find((item) => {
        if (item.entityName === "requestMessage") {
          messageTab = item;
          return;
        }
      });
      if (this.rtcrm.isNull(messageTab)) {
        this.tabs.push({
          label: "",
          entityName: "requestMessage",
          closable: true,
          isSysConfig: false,
        });
      }
    },

    //Tab
    tabRemove: function (targetName) {
      let index = 0;
      this.tabs.find((item) => {
        if (item.entityName === targetName) {
          this.tabs.splice(index, 1);
          return;
        }
        ++index;
      });
    },

    //-envirFrom
    handleSizeChange: function (val) {
      this.$set(this.input, "pageSizeEnvirFrom", val);
    },

    //-envirFrom
    handleCurrentChange: function (val) {
      this.$set(this.input, "pageIndexEnvirFrom", val);
      this.getData("envirFrom");
    },

    //-envirTo
    handleSizeChange2: function (val) {
      this.$set(this.input, "pageSizeEnvirTo", val);
    },

    //-envirTo
    handleCurrentChange2: function (val) {
      this.$set(this.input, "pageIndexEnvirTo", val);
      this.getData("envirTo");
    },

    //--
    downloadTemplate: function (envir, row) {
      let _this = this;
      this.jshelper.showLoading();
      _this.$set(_this, "loading", true);
      let input = {
        envir: envir,
        templateId: row.Id,
      };
      console.log(input);

      //#region 
      this.jshelper
        .invokeHiddenApiAsync(
          "new_hbxn_common",
          "SyncConfiguration/DownloadTemplate",
          JSON.stringify(input)
        )
        .then((res) => {
          this.jshelper.closeLoading();
          _this.$set(_this, "loading", false);
          if (this.rtcrm.isNull(res)) {
            this.rtcrm.openAlertDialog(
              ""
            );
            return;
          }
          if (res.isSuccess) {
            _this.base64ToFile(res.data, res.message);
            _this.showMessage("", "success", true);
          } else {
            _this.showMessage("", "success", true);
          }
        })
        .catch((err) => {
          _this.$set(_this, "loading", false);
          this.jshelper.closeLoading();
          this.jshelper.openAlertDialog(_this, err.message, "--");
          _this.addMessageTab(err.message);
        });
      //#endregion
    },

    //#region base64
    base64ToFile: function (base64, fileName) {
      var myBlob = this.dataURLtoBlob(base64, fileName);
      var myUrl = URL.createObjectURL(myBlob);
      this.downloadFile(myUrl, fileName);
    },

    dataURLtoBlob: function (dataurl, fileName) {
      var arr = dataurl.split(",");
      var mime = arr[0].match(/:(.*?);/);
      if (!this.rtcrm.isNull(mime) && mime.length > 0) {
        mime = mime[1];
      } else {
        let ext = this.getFileExtension(fileName);
        switch (ext) {
          case "xlsx":
            mime =
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
            break;
          case "docx":
            mime =
              "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
            break;
          default:
            this.showMessage("", "error");
            break;
        }
      }
      var base64 = !this.rtcrm.isNull(arr) && arr.length > 1 ? arr[1] : arr[0];
      var bstr = null;
      if (this.safeAtob(base64)) {
        bstr = atob(base64);
      } else if (this.safeAtob(window.atob(base64))) {
        bstr = atob(window.atob(base64));
      } else {
        bstr = atob(base64);
      }
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },

    downloadFile: function (url, name) {
      var a = document.createElement("a");
      a.setAttribute("href", url);
      a.setAttribute("download", name);
      a.setAttribute("target", "_blank");
      let clickEvent = document.createEvent("MouseEvents");
      clickEvent.initEvent("click", true, true);
      a.dispatchEvent(clickEvent);
    },

    //base64
    safeAtob: function (base64Str) {
      if (this.rtcrm.isNullOrWhiteSpace(base64Str))
        throw new Error("base64Str");

      // Base64
      const base64Regex = /^[A-Za-z0-9+/]+={0,2}$/;
      if (!base64Regex.test(base64Str)) {
        return false;
      }

      // 4'='
      while (base64Str.length % 4 !== 0) {
        base64Str += "=";
      }

      return true;
    },
    //#endregion

    //
    getFileExtension: function (filename) {
      // 
      const lastDot = filename.lastIndexOf(".");
      // 
      if (lastDot !== -1 && lastDot !== 0) {
        return filename.substring(lastDot + 1);
      } else {
        // 
        return "";
      }
    },
  },
};
</script>

<style scoped>
.main-container {
  min-height: 100%;
  background-color: white;
}

.credit-title {
  margin-top: 15px;
  text-align: center;
}

.required {
  color: red;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.box-card {
  width: 100%;
}
</style>



